
// Vendors
import Vue from "vue";
// Components
import UserLink from "./UserLink.vue";

export default Vue.extend({
  name: "PostDetailComponent",
  props: {
    post: { type: Object, required: true },
    user: { type: Object, required: true },
  },
  components: { UserLink },
  data() {
    return {
      show: true,
    };
  },
  mounted() {
    this.shouldShow();
  },
  watch: {
    post() {
      this.shouldShow();
    },
  },
  methods: {
    shouldShow() {
      this.show = Boolean(this.post) && Boolean(this.user);
    },
  },
});
